@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
:root {
  --max-width: 1190px;
  --brand-yellow: #fcaf17;
  --brand-red: #bf1e2e;
  --brand-red-dark: #a31926;
  --brand-gray: #e9ebed;
  --brand-gray-dark: #b0b3b8;
  --brand-gray-light: #f7f8f9;
  --brand-text: #111111;
  --dropdown-height: 500px;
  --font-subtitle: 22px;
  --system-black: #000000;
  --system-white: #ffffff;
  --system-gray: #cccccc;
}

* {
  outline: none;
  box-sizing: border-box;
}

html {
  overflow-y: scroll;
  box-sizing: border-box;
  font-size: 100%;
  scroll-behavior: smooth;
  background-color: white;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  color: var(--brand-text);
  font-size: 16px;
  font-optical-sizing: auto;
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  font-style: normal;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  margin-top: 0;
  letter-spacing: -0.5px;
  margin-bottom: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.3;
}

h1 {
  font-family: "Geometr415", sans-serif;
  font-size: 36px;
}
@media (max-width: 768px) {
  h1 {
    font-size: 28px;
  }
}

h2 {
  font-family: "Geometr415", sans-serif;
  font-size: 28px;
}
@media (max-width: 768px) {
  h2 {
    font-size: 24px;
  }
}
h2.under-border {
  position: relative;
  padding-bottom: 1rem;
  margin-bottom: 2rem;
}
h2.under-border:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 4px;
}
h2.under-border.yellow:after {
  background-color: var(--brand-yellow);
}
h2.under-border.red:after {
  background-color: var(--brand-red);
}

h3 {
  font-size: 22px;
  margin-bottom: 0.75rem;
  font-family: "Geometr415", sans-serif;
}
@media (max-width: 768px) {
  h3 {
    font-size: 20px;
  }
}

h4 {
  font-family: "Geometr415", sans-serif;
  font-size: 18px;
}

strong {
  font-weight: 600;
}

p {
  margin-bottom: 1rem;
}

@font-face {
  font-family: "Geometr415";
  font-style: normal;
  font-weight: bold;
  src: url("Geometr415_2_0.eot");
  src: url("Geometr415_2_0.woff2") format("woff2"), url("Geometr415_2_0.woff") format("woff"), url("Geometr415_2_0.ttf") format("truetype");
}
@font-face {
  font-family: "Geometr415";
  font-style: normal;
  font-weight: normal;
  src: url("Geometr415_0_0.eot");
  src: url("Geometr415_0_0.woff2") format("woff2"), url("Geometr415_0_0.woff") format("woff"), url("Geometr415_0_0.ttf") format("truetype");
}
#top-drawer {
  position: fixed;
  top: -160px;
  left: 0;
  width: 100%;
  z-index: 1000;
  background-color: white;
  border-bottom: 2px solid white;
  min-height: 154px;
  font-family: "Geometr415", sans-serif;
  transition: height 0.3s, border-color 0.3s, top 0.3s;
}
#top-drawer.open {
  border-color: var(--brand-red);
}
#top-drawer.peek {
  top: 0;
}
#top-drawer.scrolled {
  top: -105px;
}
@media (max-width: 1200px) {
  #top-drawer {
    display: none;
  }
}

.close-navigation {
  cursor: pointer;
  margin-top: 50px;
}

#top-navigation {
  transition: height 0.2s ease;
}
#top-navigation.opened {
  margin-top: 0;
}
#top-navigation.scrolled {
  margin-top: -105px;
}

#top-navigation .main-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: white;
  display: flex;
  justify-content: start;
  gap: 4px;
}
#top-navigation .main-menu .home-link {
  display: none;
}

#top-navigation .main-menu a {
  display: block;
  padding: 8px 20px 5px 20px;
  text-decoration: none;
  transition: background-color 0.3s;
  background-color: white;
  font-size: 1rem;
  white-space: nowrap;
  border-bottom: 3px solid transparent;
}

#top-navigation .main-menu > li {
  position: relative;
}
#top-navigation .main-menu > li ul {
  display: flex;
  flex-direction: column;
  z-index: 1000;
  position: absolute;
  min-width: 240px;
  padding-bottom: 1rem;
  opacity: 0;
  transition: opacity 0.3s;
  transition-delay: 0.1s;
  padding-top: 6px;
}
#top-navigation .main-menu > li ul li {
  border-bottom: 4px solid white;
}
#top-navigation .main-menu > li ul a {
  background-color: var(--brand-gray);
  border-left: 4px solid transparent;
  padding: 8px 20px 5px 16px;
  border-bottom: none;
}
#top-navigation .main-menu > li ul a:hover {
  background-color: white;
  border-color: var(--brand-yellow);
}
#top-navigation .main-menu > li > ul {
  top: 100%;
  left: -9999px;
}
#top-navigation .main-menu > li.active > a {
  border-color: var(--brand-yellow);
}
#top-navigation .main-menu > li:has(ul) > a:after {
  content: "\f0d7";
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  margin-left: 8px;
  font-size: 0.8em;
  color: var(--brand-yellow);
}
@media (hover: hover) {
  #top-navigation .main-menu > li:hover > ul {
    left: 0;
    opacity: 1;
  }
  #top-navigation .main-menu > li:hover > a {
    border-color: var(--brand-yellow);
  }
}
#top-navigation .main-menu > li ul li ul {
  top: 0;
  left: -9999px;
  padding-left: 4px;
  height: 100%;
}
#top-navigation .main-menu > li ul li.active > ul, #top-navigation .main-menu > li ul li:hover > ul {
  left: 100%;
  opacity: 1;
}
#top-navigation .main-menu > li ul li.active > a, #top-navigation .main-menu > li ul li:hover > a {
  background-color: white;
  border-color: var(--brand-yellow);
}

.product-spotlight {
  margin-top: 6px;
  width: 420px;
  margin-left: auto;
  display: none;
}
.product-spotlight .image-wrapper {
  aspect-ratio: 1.35/1;
  margin-left: auto;
  margin-bottom: 10px;
  background-image: url(../images/slide-bg.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}
.product-spotlight .image-wrapper .product-image {
  width: 70%;
  height: auto;
  display: block;
  max-height: 80%;
}
.product-spotlight .text-wrapper {
  border-top: 1px solid var(--brand-gray);
  padding: 0.5rem;
  text-align: center;
  font-weight: 600;
  font-size: 1.2rem;
}

#floating-nav {
  font-family: "Geometr415", sans-serif;
  position: fixed;
  top: 400px;
  right: 0;
  z-index: 1000;
}
@media (max-width: 768px) {
  #floating-nav {
    display: none;
  }
}

/* mobile navigation */
/* ================================================================= */
.mm-panels {
  --colour-links: var(--system-black);
}
.mm-panels .mm-panel {
  background-color: white;
}
.mm-panels a {
  font-size: 1rem;
}
.mm-panels .mm-btn:after,
.mm-panels .mm-btn:before {
  border-color: var(--brand-yellow) !important;
}
.mm-panels .active a {
  font-weight: bold;
  color: var(--brand-red);
}
.mm-panels > div:first-child .mm-navbar_sticky {
  display: none;
}
.mm-panels .mm-navbar_sticky > a:last-child {
  opacity: 0;
}

.mobile-buttons {
  display: none;
}

.desktop-buttons {
  font-family: "Geometr415", sans-serif;
  display: block;
}

@media (max-width: 1200px) {
  .mobile-buttons {
    display: block;
    transform: scale(1.2);
    margin-left: 10px;
    margin-top: 5px;
  }

  .desktop-buttons {
    display: none;
  }
}
.mobile-only {
  display: none !important;
}

.body-copy a {
  text-decoration: underline;
}
@media (hover: hover) {
  .body-copy a:hover {
    text-decoration: none;
  }
}

.desktop-only {
  display: block;
}

.display-none {
  display: none;
}

@media (max-width: 768px) {
  .mobile-only {
    display: block !important;
  }

  .desktop-only {
    display: none !important;
  }
}
.preload {
  display: none;
}

.small-print * {
  font-size: 0.95rem;
}

.pre-script {
  opacity: 0;
}

.imagery {
  width: 100%;
  display: block;
}

.app-loading {
  opacity: 0;
}

.tab-item-app {
  display: none;
}
@media screen and (min-width: 1200px) {
  .tab-item-app {
    display: block;
  }
}

.section-title {
  max-width: var(--max-width);
  margin-left: auto;
  margin-right: auto;
  padding: 0 16px;
  width: 100%;
}

.page-intro {
  margin-bottom: 1rem;
}
@media screen and (min-width: 768px) {
  .page-intro {
    margin-bottom: 2rem;
  }
}

.departments-title {
  margin-bottom: 1rem;
  border-bottom: 1px solid var(--brand-gray);
}
@media screen and (min-width: 768px) {
  .departments-title {
    margin-bottom: 2rem;
  }
}

.shaded-section {
  background-color: var(--brand-gray-light);
  padding: 2rem 0;
  margin-bottom: 1rem;
}
.shaded-section .section {
  margin-bottom: 0;
}
@media screen and (min-width: 992px) {
  .shaded-section {
    margin-bottom: 3rem;
  }
}

.special-background {
  background-color: var(--brand-red);
  background-image: url("../images/bg-about-busy.jpg");
  color: white;
  padding-top: 30px;
}
.special-background .section {
  display: flex;
  flex-direction: column;
}
.special-background .text-wrapper,
.special-background .image-wrapper {
  flex: 1;
}
.special-background .image-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media screen and (min-width: 768px) {
  .special-background {
    padding-top: 60px;
  }
  .special-background .section {
    flex-direction: row;
    gap: 2rem;
  }
  .special-background .text-wrapper {
    max-width: 50%;
  }
  .special-background .image-wrapper {
    justify-content: center;
    align-items: center;
  }
  .special-background .image-wrapper img {
    max-width: 50%;
  }
  .special-background .image-wrapper .top-image {
    margin-left: 10%;
  }
  .special-background .image-wrapper .bottom-image {
    margin-right: 10%;
  }
}

.special-background-2 {
  padding-top: 2rem;
}
.special-background-2 .section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media screen and (min-width: 768px) {
  .special-background-2 {
    padding-top: 3rem;
  }
  .special-background-2 .section {
    flex-direction: row-reverse;
    gap: 2rem;
  }
  .special-background-2 .text-wrapper,
.special-background-2 .image-wrapper {
    flex: 1;
  }
  .special-background-2 img {
    margin-top: 3rem;
    max-width: 480px;
    margin-right: auto;
  }
}

.special-background-3 {
  background-color: var(--brand-gray-light);
  padding-top: 2rem;
  background-image: url("../images/fabric.png");
}
@media screen and (min-width: 768px) {
  .special-background-3 {
    padding-top: 3rem;
  }
  .special-background-3 .section {
    background-image: url("../images/logo-slanted.png");
    background-repeat: no-repeat;
    background-position: bottom -50px right 5px;
    background-size: 600px auto;
    margin-bottom: 0;
    min-height: 500px;
  }
  .special-background-3 .text-wrapper {
    width: 50%;
  }
}

.special-background-4 {
  padding-top: 2rem;
}
.special-background-4 .section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media screen and (min-width: 768px) {
  .special-background-4 {
    padding-top: 3rem;
    min-height: 600px;
    background: linear-gradient(250deg, #ffffff 50%, var(--brand-red) 50%);
    padding-top: 3rem;
    position: relative;
  }
  .special-background-4 img {
    width: 900px;
    height: auto;
    position: absolute;
    top: 50%;
    right: 40%;
    transform: translateY(-50%);
  }
  .special-background-4 .section {
    flex-direction: row;
    gap: 20rem;
  }
  .special-background-4 .text-wrapper,
.special-background-4 .image-wrapper {
    width: 50%;
  }
  .special-background-4 .image-wrapper {
    display: block;
  }
}

.section {
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 0 auto 30px auto;
  max-width: var(--max-width);
  position: relative;
  width: 100%;
}
.section.shaded-copy {
  background-color: var(--brand-gray-light);
}
@media screen and (min-width: 992px) {
  .section .padded-copy {
    padding-right: 20%;
  }
}
.section.has-sub-sections {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 60px;
}
.section.has-sub-sections .sub-section-wrapper {
  width: 100%;
}
.section.has-sub-sections .content {
  min-height: 300px;
  position: relative;
  border: 1px solid var(--brand-gray);
  padding: 12px 1rem;
  border-radius: 4px;
}
.section.has-sub-sections .content .text-wrapper {
  z-index: 11;
  position: relative;
}
.section.has-sub-sections .content .content-img {
  position: absolute;
  bottom: -12px;
  right: 0;
  z-index: 10;
  max-width: 80%;
}
@media screen and (min-width: 768px) {
  .section {
    margin-bottom: 60px;
  }
  .section.special-chevron {
    background-image: url("../images/bg-about-chevrons.png");
    background-repeat: no-repeat;
    background-position: bottom -40px right 50px;
    background-size: 300px 300px;
    margin-bottom: 0;
    padding-bottom: 2rem;
    padding-right: 500px;
  }
  .section.has-sub-sections {
    flex-direction: row;
  }
  .section.has-sub-sections .content {
    padding: 1rem 1.2rem;
  }
  .section.has-sub-sections .content .text-wrapper {
    width: 80%;
  }
  .section.has-sub-sections .content .content-img {
    bottom: -1rem;
    max-width: 60%;
  }
}

.section-padding {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
@media screen and (min-width: 768px) {
  .section-padding {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}

.breadcrumbs {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 1rem;
  margin: 0 auto;
  max-width: var(--max-width);
  width: 100%;
  font-family: "Geometr415", sans-serif;
}
.breadcrumbs ol {
  border-top: 1px solid var(--brand-gray);
  display: flex;
  font-size: 0.9rem;
}
.breadcrumbs ol li {
  display: flex;
  align-items: center;
}
.breadcrumbs ol li::after {
  content: "/";
  margin-left: 0.5em;
  margin-right: 0.5em;
  color: var(--brand-yellow);
}
.breadcrumbs ol li:last-child::after {
  content: none;
}
.breadcrumbs ol li:last-child {
  font-weight: bold;
}
.breadcrumbs ol a {
  padding: 0.45rem 0;
  display: inline-block;
}
@media (hover: hover) {
  .breadcrumbs ol a:hover {
    text-decoration: underline;
  }
}

.columned-content {
  margin-bottom: 1rem;
}
@media screen and (min-width: 768px) {
  .columned-content {
    columns: 2;
    column-gap: 4rem;
    margin-bottom: 2rem;
  }
}

.introduction-wrapper,
.title-wrapper,
.page-intro {
  max-width: var(--max-width);
  margin: 0 auto;
  padding: 2px 1rem 1rem 1rem;
  width: 100%;
}

.introduction {
  border-bottom: 1px solid var(--brand-gray-dark);
  padding-bottom: 0.5rem;
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0;
  overflow: hidden;
}
.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.column-two {
  padding-bottom: 2rem;
}
@media (min-width: 992px) {
  .column-two {
    columns: 2;
    column-gap: 4rem;
  }
}

.column-three {
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .column-three {
    columns: 2;
    column-gap: 3rem;
  }
}
@media (min-width: 992px) {
  .column-three {
    columns: 3;
    column-gap: 3rem;
  }
}

.external-link {
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .external-link {
    padding-bottom: 2rem;
  }
}

.bttn {
  font-family: "Geometr415", sans-serif;
  font-weight: 100;
  padding: 10px 16px 8px;
  font-size: 1rem;
  border-radius: 2px;
  cursor: pointer;
  line-height: 1;
  white-space: nowrap;
  text-decoration: none !important;
  text-align: center;
  border: 1px solid var(--brand-red);
  transition: all 0.3s ease-in-out;
  display: inline-flex;
  gap: 6px;
  align-items: center;
  background-color: var(--brand-red);
  color: white;
}
.bttn.small {
  font-size: 0.8rem;
}
.bttn.bordered-bttn {
  border: 1px solid #d68188;
}
.bttn.transparent-bttn {
  background-color: transparent;
  color: white;
  text-shadow: 0 0 3px #000;
  border: 1px solid #c9c9c9;
}
@media (hover: hover) {
  .bttn.transparent-bttn:hover {
    background-color: white;
    color: var(--brand-red-dark);
    border-color: white;
    text-shadow: none;
  }
}
.bttn[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}
.bttn span:last-child {
  color: var(--brand-yellow);
  font-size: 1.1rem;
}
.bttn > span:first-child:has(i) {
  margin-right: 4px;
}
@media (hover: hover) {
  .bttn:hover {
    background-color: var(--brand-red-dark);
    border-color: var(--brand-red-dark);
  }
  .bttn:hover span:last-child {
    color: #f8b52d;
  }
}

.image-link {
  display: block;
  overflow: hidden;
  border-radius: 2px;
}
.image-link img {
  display: block;
  width: 100%;
  height: auto;
  transition: all 0.3s ease-in-out;
}
@media (hover: hover) {
  .image-link:hover img {
    transform: scale(1.03);
  }
}

.item-pod {
  border: 1px solid var(--brand-gray);
  border-radius: 2px;
  display: flex;
  flex-direction: column;
}
.item-pod .title {
  background-color: var(--brand-gray);
  padding: 0.5rem;
  margin: 0;
  font-size: 1rem;
  height: 3.25rem;
  line-height: 1.3;
  flex-shrink: 0;
}
.item-pod .details {
  padding: 10px 12px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  flex-grow: 1;
}
.item-pod .details .image-copy-wrapper {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}
.item-pod .details .image-wrapper {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.item-pod .details img {
  width: 120px;
  height: auto;
  aspect-ratio: 5/4;
  min-width: 120px;
}
.item-pod .details .copy-wrapper {
  font-size: 0.9rem;
}
.item-pod .details .price {
  font-weight: bold;
}
.item-pod .details .action {
  margin-top: auto;
}
.item-pod .action {
  border-top: 1px solid var(--brand-gray);
  padding-top: 10px;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.items-grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1rem;
}
@media screen and (min-width: 768px) {
  .items-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .items-grid .item-pod {
    height: 460px;
  }
  .items-grid .item-pod .copy-wrapper {
    height: 250px;
    overflow-y: scroll;
  }
}
@media screen and (min-width: 1024px) {
  .items-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* ================================================================= */
/* .back-to-top */
.back-to-top {
  display: inline-block;
  width: 44px;
  height: 44px;
  text-align: center;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
}
@media (max-width: 992px) {
  .back-to-top {
    display: none;
  }
}
.back-to-top:after {
  content: "\f077";
  font-family: FontAwesome;
